import { Checkbox } from '../../components/Button/Button'
export const CheckBoxReadOnly = ({ item }) => {
  return (
    <div className="content__options">
      <Checkbox
        text={' '}
        checked={item.isNeedChildChair}
        readonly
      />
      <Checkbox text={' '} checked={item.isRightWheel} readonly />
      <Checkbox text={' '} checked={item.isFullTank} readonly />
    </div>
  )
}
