import { getDate } from '../../components/GetDate/GetDate'

export const OrderContentColumn = ({ item }) => {
  return (
    <div className="content__column">
      <p className="text-dark">
        {item.carId ? item.carId.name : '  '}
        <span className="text-light">  </span>
        <span className="text-dark">
          {item.cityId ? item.cityId.name : ' '}
        </span>
        <span className="text-light">
          , {item.pointId ? item.pointId.address : ' '}
        </span>
      </p>
      <p className="text-light">
        {getDate(item.dateFrom)} - {getDate(item.dateTo)}
      </p>
      <p className="text-dark">
        <span className="text-light"> </span> {item.color}
      </p>
      <p className="text-dark">
        <span className="text-light"> </span>
        {item.orderStatusId ? item.orderStatusId.name : ' '}
      </p>
    </div>
  )
}
